/* окно  при нажатии на меню*/
#dialog {
  /* растягивает на весь экран черный бекграунд */
  overflow: hidden;
  position: fixed;
  z-index: 10000;
  width: 100%;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.15);
  transition: visibility 0s linear 0.5s, opacity 0.5s linear;
  opacity: 0;
  visibility: hidden;
  backdrop-filter: blur(5px);
}

.dialog_state {
  visibility: hidden;
  opacity: 0;
  display: none;
}

.dialog_state:checked + #dialog,
#dialog.dialog_open {
  transition-delay: 0s;
  opacity: 1;
  visibility: visible;
}

#dlg-back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  width: 100vw;
  height: 100vh;
  cursor: pointer;
}

/* размеры окна */
